import * as getters from './getters.js'; // import ë°©ë²•1
import mutations from './mutations.js'; // import ë°©ë²•2
import actions from './actions.js';

const storage = {
  fetch() {
    let arr = [];
    if (localStorage.getItem('todoItems') !== null) {
      arr = JSON.parse(localStorage.getItem('todoItems'));
    }
    return arr;
  },
};

const state = {
  todoItems: storage.fetch(),
};

export default {
  state,
  getters,
  mutations,
  actions,
};
